// Here, more than any other area, I'm cutting some corners (at least,
// for now). As it stands, you're always logged in as one user. There's
// no way to create accounts or log out.
//
// Later, I may make a simplified auth system, where you can log in as
// any of the arbitrary users, to ensure that all the reducers update

import { combineReducers } from 'redux';

import {
    DELETE_TEST_CASE,
    SAVE_TEST_CASE
} from '../actions';

const initialState = {
    testcases: [
    ]
};

// TO BE REMOVEED
const types = [
    { key: 'PVC_Remote', Domain: 'Storage PVC', SubDomain: 'Remote' },
    { key: 'PVC_Local', Domain: 'Storage PVC', SubDomain: 'Local' },
    { key: 'PVC_Mirrored', Domain: 'Storage PVC', SubDomain: 'Mirrored' },
    { key: 'SR', Domain: 'Storage Remote', SubDomain: 'Remote' },
    { key: 'SS', Domain: 'Storage Snapshot', SubDomain: 'Snapshot' },
    { key: 'N_HostNetwork', Domain: 'Network', SubDomain: 'Host Network' },
    { key: 'N_Endpoint', Domain: 'Network', SubDomain: 'Endpoint' },
    { key: 'AT_Qos', Domain: 'Qos', tag: 'Additional Test' },
    { key: 'M_Migration', Domain: 'Management', SubDomain: 'Master Migration' },
    { key: 'MZ_ZoneCapability', Domain: 'Multizone Cluster', SubDomain: 'Zone Capability' },
    { key: 'MZ_BasicNW', Domain: 'Multizone Cluster', SubDomain: 'Basic Networking' },
]
const sortTestCaseByDomain = (tc) => {
    types.forEach(item => {
        if (!tc.Domain) {
            if (tc.TcID.search(item.key) !== -1) {
                tc = { ...tc, Domain: item.Domain, SubDomain: item.SubDomain, tag: item.tag, Setup: item.Setup ? item.Setup : 'Common' }
            }
        }
    });
    return tc;
}
// ////////////////////
// Modifiers //////////
// //////////////////

// ////////////////////
// Reducers //////////
// //////////////////
function all(state = initialState.testcases, action) {
    switch (action.type) {
        case SAVE_TEST_CASE:
            console.log('current state ', state)
            console.log('saving id ', action.payload.id)
            console.log('saving ', action.payload.data);
            let tc = action.payload.data;
            if (tc) {
                tc = sortTestCaseByDomain(tc);
            }

            state.push(tc);
            return [...state];

        case DELETE_TEST_CASE:
            let found = null;
            state.forEach((item, index) => {
                if (item.ReleaseNumber === action.payload.id) {
                    found = index;
                }
            });
            if (found !== null) {
                state.splice(found, 1);
            }
            return [...state];
        default:
            return state;
    }
}

export const testcaseReducer = combineReducers({
    all
});

// ////////////////////
// Selectors //////////
// //////////////////
export const getTestSummary = (state) => {
    return {
        totalTests: state.testcase.all ? state.testcase.all.length : 0,
        totalBugs: state.testcase.all ? state.testcase.all.filter(item => item.Bugs !== -1).length : 0,
        totalPassed: state.testcase.all ? state.testcase.all.filter(item => item.Result === 'Pass').length : 0
    }
}

export const getTestDomains = (state) => {
    let doughnuts = [];
    let domainCounted = [];
    let tcs = state.testcase.all;
    if (tcs && tcs.length) {
        types.forEach((item, index) => {
            if (!domainCounted.includes(item.Domain)) {
                domainCounted.push(item.Domain);
                let domainTCs = tcs.filter(each => each.Domain === item.Domain);
                doughnuts.push({
                    data: {
                        labels: [
                            'Fail',
                            'Pass',
                            'Skipped',
                        ],
                        datasets: [
                            {
                                data: [
                                    domainTCs.filter(each => each.Result === 'Fail').length,
                                    domainTCs.filter(each => each.Result === 'Pass').length,
                                    domainTCs.filter(each => each.Result === 'Skipped').length,
                                ],
                                backgroundColor: [
                                    '#FF6384',
                                    '#36A2EB',
                                    '#FFCE56',
                                ],
                                hoverBackgroundColor: [
                                    '#FF6384',
                                    '#36A2EB',
                                    '#FFCE56',
                                ],
                            }],
                    },
                    title: item.Domain
                })
            }

        })
    }
    return doughnuts;
}

